import React, { useState, useEffect } from 'react';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import Link from '@material-ui/core/Link';
import { useSelector, useDispatch } from 'react-redux';
import { FormHelperText } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import * as Yup from 'yup';
import Form from '../../../_pxp/components/Form/Form';
import LoadingScreen from '../../../_pxp/components/LoadingScreen';
import { startLogin } from '../../../_pxp/actions/auth';
import useSettings from '../../../_pxp/hooks/useSettings';
import '../styles/style.css';
import SocialLogin from '../SocialLogin';

const PxpLoginPageHeader = () => (
  <div className="logo-container">
    vouz
    <span className="logo-legend">TENEMOS UNA CITA</span>
  </div>
);
const useStyles = makeStyles({
  dialogTitleRoot: {
    padding: '0px',
    textAlign: 'center',
  },
  dialogContentRoot: {
    padding: '0px',
    // overflowX: 'hidden',
  },
  separatorP: {
    width: '100%',
    textAlign: 'center',
    borderBottom: '1px solid #e0e0e0',
    lineHeight: '.1em',
    fontWeight: '500',
    margin: '30px 0 30px 0',
  },
  separatorSpan: {
    padding: '0 10px',
    color: '#606060',
    background: '#fff',
    fontSize: '14px',
  },
});

export default ({ open: popen, username }) => {
  const { settings } = useSettings();
  const [loadingScreen, setLoadingScreen] = useState(false);
  const [open] = React.useState(popen);
  const [error, setError] = React.useState('');
  const sessionDied = useSelector((state) => state.auth.sessionDied);
  const dispatch = useDispatch();
  const classes = useStyles();
  const handleLogin = (login, password) => {
    const { language } = settings;
    dispatch(startLogin({ login, password, language })).then((errorMsg) => {
      if (errorMsg !== 'success') {
        setError(errorMsg);
        setLoadingScreen(false);
      }
    });
  };

  useEffect(() => {
    if (!sessionDied && !!username) {
      setLoadingScreen(false);
    }
  }, [sessionDied, loadingScreen, username]);

  const handleClose = () => {};

  const userForm = {
    columns: {
      username: {
        type: 'TextField',
        label: 'Username',
        autoFocus: true,
        initialValue: username || '',
        disabled: !!username,
        gridForm: { xs: 12, sm: 12 },
        variant: 'outlined',
        validate: {
          shape: Yup.string().required('Username is Required'),
        },
      },
      password: {
        type: 'TextField',
        typeTextField: 'password',
        label: 'Password',
        initialValue: '',
        gridForm: { xs: 12, sm: 12 },
        variant: 'outlined',
        autoFocus: !!username,
        validate: {
          shape: Yup.string().required('Password is Required'),
        },
      },
    },
    resetButton: false,
    submitLabel: 'Login',
    onEnterSubmit: true,
    onSubmit: ({ values }) => {
      setLoadingScreen(true);
      handleLogin(values.username, values.password);
    },
  };
  const loginFormContainerStyle = {
    padding: '32px',
  };

  return (
    <>
      <Dialog
        open={open || sessionDied}
        onClose={handleClose}
        aria-labelledby="login-dialog-title"
      >
        <DialogTitle classes={{ root: classes.dialogTitleRoot }}>
          <PxpLoginPageHeader />
        </DialogTitle>
        <DialogContent classes={{ root: classes.dialogContentRoot }}>
          <div className="login-form-container" style={loginFormContainerStyle}>
            <Form data={userForm} dialog />
            {error && <FormHelperText error>{error}</FormHelperText>}
            <Link href="/forgot">Forgot password?</Link>
            <p className={classes.separatorP}>
              <span className={classes.separatorSpan}>o login with</span>
            </p>
            <div
              style={{ width: '100%', align: 'center', marginLeft: '-10px' }}
            >
              <SocialLogin open />
            </div>
          </div>
        </DialogContent>
      </Dialog>
      {loadingScreen && <LoadingScreen />}
    </>
  );
};
