import React from 'react';
import useJsonStore from '../../../_pxp/hooks/useJsonStore';

const systemUrl = 'citas/Dama';

export const damaServices = {
  setDisponibilidad: (id_dama) => ({
    url: `${systemUrl}/cambiarDisponibilidadDama`,
    load: true,
    params: { id_dama }
  }),
  setLocation: ({ id_dama, latitud, longitud }) => ({
    url: `${systemUrl}/fijarUbicacionDama`,
    load: true,
    params: {
      id_dama,
      disponible_latitud: latitud,
      disponible_longitud: longitud,
    }
  }),
  getData: () => ({
    url: `${systemUrl}/listarEstadoDama`,
    load: true,
    params: {}
  }),
  changeStateCita: (id_cita, estado_cita, latitud_estado, longitud_estado) => ({
    url: `citas/Cita/insertarCita`,
    load: true,
    params: {
      id_cita,
      estado_cita,
      latitud_estado,
      longitud_estado,
    }
  })
};

export const useServicesDama = (service) => {
  const { data, loading, state, set: setParams, error } = useJsonStore(service || { load: false });
  // const [state, setService] = useJsonStore(state);

  return {
    data: data && data.datos ? data.datos : [],
    loading,
    state,
    setParams
  };
}
