import React, { forwardRef } from 'react';
import { Button } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import FacebookIcon from './icons/svg/FacebookIcon';
import GoogleIcon from './icons/svg/GoogleIcon';

const useStyles = makeStyles({
  
});

const SocialLogin = forwardRef(() => {
  let webViewClient = '';

  const { standalone } = window.navigator;
  const userAgent = window.navigator.userAgent.toLowerCase();
  const safari = /safari/.test(userAgent);
  const ios = /iphone|ipod|ipad/.test(userAgent);
  const classes = useStyles();

  if (ios) {
    if (!standalone && safari) {
      webViewClient = 'Safari';
    } else if (!standalone && !safari) {
      webViewClient = 'iOSWebView';
    }
  } else if (userAgent.includes('wv')) {
    webViewClient = 'AndroidWebView';
  } else {
    webViewClient = 'RegularBrowser';
  }

  const handleFacebookLogin = () => {
    if (window.Mobile) {
      window.Mobile.facebookLogin();
      // return false;
    }
  };

  const handleGoogleLogin = () => {
    if (window.Mobile) {
      window.Mobile.googleLogin();
      // return false;
    }
  };

  return (
    <>
      <div className={classes.socialContainer}>
        <Button
          variant="contained"
          color="secondary"
          className={classes.facebookButton}
          onClick={handleFacebookLogin()}
          startIcon={
            <FacebookIcon
              style={{ paddingTop: '8px' }}
              width={30}
              fill="#ffffff"
            />
          }
        >
          Facebook
        </Button>

        <Button
          variant="contained"
          color="secondary"
          className={classes.googleButton}
          onClick={handleGoogleLogin()}
          startIcon={
            <GoogleIcon
              style={{ paddingTop: '8px' }}
              width={30}
              fill="#ffffff"
            />
          }
        >
          Google
        </Button>
      </div>
    </>
  );
});

export default SocialLogin;
