import { createStore, combineReducers, applyMiddleware } from 'redux';
import thunk from 'redux-thunk';
import authReducer from '../../_pxp/reducers/auth';
import appReducer from '../../_pxp/reducers/app';
import notifyReducer from '../../_pxp/reducers/notify';
import citasReducer from './citas-reducer';

export default () =>
  createStore(
    combineReducers({
      app: appReducer,
      auth: authReducer,
      notify: notifyReducer,
      citas: citasReducer,
    }),
    applyMiddleware(thunk),
  );