import {
  getDataDamaService,
  updateStateCitaService,
  updateDiponibilidadDama,
  updateLocationDama,
} from "./services";

export const GET_DATA_DAMA = '[citas] GET_DATA_DAMA';
export const ERROR_DATA_DAMA = '[citas] ERROR_DATA_DAMA';
export const SET_DATA_DAMA = '[citas] SET_DATA_DAMA';
export const UPDATE_CITA_STATE = '[citas] UPDATE_CITA_STATE';
export const ERROR_CITA_STATE = '[citas] ERROR_CITA_STATE';


export const requestDataDama = () => async (dispatch) => {
  dispatch(getDataDama());
  const data = await getDataDamaService();
  if (data.error) {
    dispatch(errorDataDama(data.detail.message));
  } else {
    dispatch(setDataDama(data.datos[0]));
  }
}

export const getDataDama = () => ({ type: GET_DATA_DAMA });
export const errorDataDama = (error) => ({ type: ERROR_DATA_DAMA, payload: error });
export const setDataDama = (data) => ({ type: SET_DATA_DAMA, payload: data });


export const updateCitaStateAction = (idCita, newState, lat, lng) => async (dispatch) => {
  const data = await updateStateCitaService(idCita, newState, lat, lng);
  if (data.error) {
    dispatch(errorCitaState(data.detail.message));
  } else {
    dispatch(updateCitaState());
  }
};

export const updateLocationAction = (idDama, lat, lng) => async (dispatch) => {
  const data = await updateLocationDama(idDama, lat, lng);
  if (data.error) {
    dispatch(errorCitaState(data.detail.message));
  } else {
    dispatch(updateCitaState());
  }
};

export const updateDisponibilidadAction = (idDama) => async (dispatch) => {
  const data = await updateDiponibilidadDama(idDama);
  if (data.error) {
    dispatch(errorCitaState(data.detail.message));
  } else {
    dispatch(updateCitaState());
  }
};

export const errorCitaState = (error) => ({ type: ERROR_CITA_STATE, payload: error });
export const updateCitaState = () => ({ type: UPDATE_CITA_STATE });
