import {
  GET_DATA_DAMA,
  SET_DATA_DAMA,
  ERROR_DATA_DAMA,
  UPDATE_CITA_STATE,
  ERROR_CITA_STATE
} from './citas-actions';
const initialState = {
  dataDama: {},
  updateDataDama: true,
};
const citasReducer = (state = initialState, action) => {
  switch (action.type) {
    case GET_DATA_DAMA: {
      return {
        ...state,
        dataDama: { loading: true, error: false },
        updateDataDama: false,
      };
    }
    case ERROR_DATA_DAMA: {
      return {
        ...state,
        dataDama: { loading: false, error: action.payload },
        updateDataDama: false,
      };
    }
    case SET_DATA_DAMA: {
      return {
        ...state,
        dataDama: { loading: false, error: false, ...action.payload },
        updateDataDama: false,
      };
    }
    case ERROR_CITA_STATE: {
      return {
        ...state,
        updateDataDama: false,
        dataDama: {
          ...state.dataDama, error: action.payload
        }
      };
    }
    case UPDATE_CITA_STATE: {
      return {
        ...state,
        updateDataDama: true
      };
    }
    default: return { ...state };
  }
};

export default citasReducer;