import { lazy } from 'react';
import Login from '../containers/components/LoginDialog';

const pages = {};

pages.CTA__Discover = {
  path: '/discover',
  component: lazy(() => import('./Discover')),
};

// pages.CTA__Nearby = {
//   path: '/map',
//   component: lazy(() => import('./Nearby')),
// };

pages.CTA__Nearby = {
  path: '/map',
  component: lazy(() => import('./Nearby')),
};

pages.CTA__Citas = {
  path: '/citas',
  component: lazy(() => import('./ListOfDates')),
  detailPages: [
    {
      path: '/detail/:id',
      component: lazy(() =>
        import('./Detail/Date'),
      ),
    },
  ],
};

pages.CTA__Home = {
  path: '/home',
  component: lazy(() => import('./HomeDama')),
  eventListener: (userId) => `user_notifications__${userId}`,
  showNotify: true,
};

pages.CTA__Notify = {
  path: '/notifications',
  component: lazy(() => import('./HomeDama')),
  eventListener: (userId) => `user_notifications__${userId}`,
  showNotify: true,
};

pages.CTA__Detail = {
  path: '/detail/:id',
  component: lazy(() => import('./Detail/Date')),
};

pages.CTA__Login = Login;

export default pages;
