import Pxp from '../../Pxp';
import { damaServices } from '../pages/HomeDama/services-dama';

export const getDataDamaService = () => {

  return Pxp.apiClient
    .doRequest(
      damaServices.getData()
    );
  // .then((data) => {
  //   if (data.error) {
  //     return data.detail.message;
  //   }
  //   return 'success';
  // });
};

export const updateStateCitaService = (idCita, newState, lat, lng) => Pxp.apiClient
  .doRequest(
    damaServices.changeStateCita(idCita, newState, lat, lng)
  );

export const updateLocationDama = (id_dama, latitud, longitud) => Pxp.apiClient
  .doRequest(
    damaServices.setLocation({ id_dama, latitud, longitud })
  );

export const updateDiponibilidadDama = (idDama) => Pxp.apiClient
  .doRequest(
    damaServices.setDisponibilidad(idDama)
  );

